<template>
  <div class="nav navMenu" :class="{ 'showNavMenu': showValue }">
    <div class="showNav" @click="updateChildValue"></div>
    <ul>
      <li class="login">
        <router-link to="/">
          <div class="nav-img"><img src="@/assets/img/team/team-01.png" alt=""></div>
          <div>Game</div>
        </router-link>
      </li>
      <li @click="showBlindBox">
        <router-link to="/friends">
          <div class="nav-img"><img src="@/assets/img/team/team-02.png" alt=""></div>
          <div>Friends</div>
        </router-link>
      </li>
      <li @click="showBlindBox">
        <router-link to="/quest">
          <div class="nav-img"><img src="@/assets/img/team/team-03.png" alt=""></div>
          <div>Quest</div>
        </router-link>
      </li>
      <li @click="showBlindBox">
        <router-link to="/ranking">
          <div class="nav-img"><img src="@/assets/img/team/team-04.png" alt=""></div>
          <div>Ranking</div>
        </router-link>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'NavMenu',
  props: {
    showValue: {
      type: String,
      required: true
    }
  },
  methods: {
    showBlindBox: function() {
      console.log('showBlindBox');
      const canvas = document.querySelector('canvas');
      const score = document.getElementById('score');
      if (canvas) {
        canvas.remove();
      }
      if (score) {
        score.remove();
      }
    },
    updateParentValue(value) {
      this.parentValue = value;
    },
    updateChildValue() {
      const navMenu = document.querySelector('.navMenu');
		  navMenu.classList.remove('showNavMenu');
      this.$emit('childClick', false);
    }
  }
}
</script>

<style lang="scss" scoped>
/* 样式代码 */
.nav {
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 999;
  width: 100%;
  padding-bottom: .5rem;
  background-color: #FFF;
  transition: bottom 0.8s ease-in-out;

  ul {
    width: 100%;
    padding: 0;
    margin: 0;
    display: flex;
    justify-content: space-around;

    li {
      text-align: center;
      list-style: none;
      width: 33.3%;
      font-size: .8rem;
      cursor: pointer;
      color: #fff;
    }

    li:hover {
      color: #fff;
    }

    li .nav-img{
      height: 3rem;
      display: flex;
      justify-content: center;
      align-items: center;
    }

    li img {
      padding: 0;
      margin: 0;
      width: 1.9rem;
    }
  }
}
.navMenu ul, .blindBox ul {
  list-style: none;
  padding: 0;
}

.navMenu ul li, .blindBox ul li {
  display: inline-block;
  margin: .3rem;
}

.navMenu ul li div, .blindBox ul li div {
  text-align: center;
  color: #161616;
}

.showNavMenu {
  bottom: -7.3rem;
}

.showNav {
  content: "";
  position: absolute;
  top: -6rem;
  left: calc(50% - 2rem);
  width: 0;
  height: 0;
  border-left: 1.5rem solid transparent;
  border-right: 1.5rem solid transparent;
  border-bottom: 1.5rem solid black;
  display: none;
}

.showNavMenu .showNav {
  display: none;
  // display: block;
}
</style>