<template>
  <div class="quest overflowyauto">
    <div class="quest-inner">
      <div class="wallet" @click="openWallet()"></div>
      <div class="user">
        <div class="user-img">
          <img src="@/assets/img/quest/2.png" alt="" srcset="">
        </div>
        <div class="user-name" @click="setSwitchNetwork()">SuperSkr</div>
        <div class="user-integral" @click="getUserData()">
          <img src="@/assets/img/icons/integral.svg" alt="" srcset="">
          <span>+233</span>
        </div>
        <div class="user-bg"></div>
      </div>
      <div class="quest-title">Daily Tasks</div>
      <div class="list">
        <ul>
          <li v-for="(item,index) in dailyList" :key="index">
            <div class="listimg" :style="{ backgroundImage: 'url(' + item.avatar + ')' }"></div>
            <div class="name">
              <div class="title">{{item.name}}</div>
              <div class="integral">{{item.num}}</div>
            </div>
            <div class="listtype"><div :class="{'active':item.actrve}"></div></div>
          </li>
        </ul>
      </div>
      <div class="quest-title">Basic Tasks</div>
      <div class="list">
        <ul>
          <li v-for="(item,index) in basicList" :key="index">
            <div class="listimg" :style="{ backgroundImage: 'url(' + item.avatar + ')' }"></div>
            <div class="name">
              <div class="title">{{item.name}}</div>
              <div class="integral">{{item.num}}</div>
            </div>
            <div class="listtype"><div :class="{'active':item.actrve}"></div></div>
          </li>
        </ul>
      </div>
    </div>
    <nav-menu :showValue="false"></nav-menu>
    <all-wallet :showWallet="showWallet" @childClick="updateParentValue"></all-wallet>
  </div>
</template>

<script>
import { ref } from 'vue';
import NavMenu from '@/components/NavMenu.vue'
import AllWallet from '@/components/AllWallet.vue'
import RewardDistributorAbi from '@/abis/RewardDistributorAbi'
import { getUserAgent } from '@/lib/isMobileDevice'
import { useWeb3Modal, useWeb3ModalAccount, useSwitchNetwork, useWeb3ModalProvider } from '@web3modal/ethers/vue'
import { BrowserProvider, Contract, formatUnits } from 'ethers'


const routeUrl = process.env.ROUTE_URL || '../';

export default {
  name: 'Quest',
  components: {
    NavMenu,
    AllWallet
  },
  data() {
    return {
      dailyList : [
        { name: 'Daily Check', avatar: routeUrl + 'assets/img/icons/time.svg',id: 1, num: 100, actrve: true },
        { name: 'Turn to offcial website', avatar: routeUrl + 'assets/img/quest/5.png',id: 2, num: 200, actrve: false },
        { name: 'Jumper bot user', avatar: routeUrl + 'assets/img/quest/6.png',id: 3, num: 300, actrve: true },
        { name: 'Daily Share', avatar: routeUrl + 'assets/img/quest/7.png',id: 4, num: 400, actrve: false },
      ],
      basicList : [
        { name: 'Connect Wallet', avatar: routeUrl + 'assets/img/quest/8.png',id: 1, num: 10, prize: 10, actrve: false },
        { name: 'Join X Group', avatar: routeUrl + 'assets/img/quest/9.png',id: 2, num: 50, prize: 60, actrve: true },
        { name: 'Follow on Twitter', avatar: routeUrl + 'assets/img/quest/10.png',id: 3, num: 100, prize: 130, actrve: false },
        { name: 'Retweet a Post', avatar: routeUrl + 'assets/img/quest/11.png',id: 3, num: 100, prize: 130, actrve: true },
      ],
      walletAddress: '',
      getModal: useWeb3Modal(),
      showWallet: false,
      userAgent: '',
      isMobileDevice: getUserAgent(),
    };
  },
  mounted() {
    
  },
  methods: {
    // 打开钱包
    openWallet() {
      // 浏览器用这个
      //getModal.open();
      // 移动端用这个
      if (this.isMobileDevice) {
        this.showWallet = true;
      } else {
        this.getModal.open();
      }
    },
    // 获取用户数据
    async getUserData() {
      const { address, chainId, isConnected } = useWeb3ModalAccount()
      localStorage.setItem('userAddress', address.value)
      console.log(address.value, chainId.value, isConnected.value)
    },
    // 切换链
    async setSwitchNetwork() {
      try {
        const chainId = 1
        const { switchNetwork } = useSwitchNetwork()
        switchNetwork(chainId)
      } catch (err) {
        console.log(err)
      }
    },
    updateParentValue(value) {
      this.showWallet = value;
      console.log('showWallet', value);
    },
  }
}
</script>
